* {
  box-sizing: border-box;
}

body,
html {
  width: 100%;
  height: 100%;
}

html {
  font-size: 18px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  padding: 0;
  margin: 0;
}

ul li {
  list-style: none;
}

p {
  margin: 0;
}

svg {
  display: block;
}

button {
  cursor: pointer;
  padding: 0;
  background-color: initial;
  border: none;
  outline: none;
  font-family: 'ubuntu', Helvetica, sans-serif;
  font-weight: 300;
  font-style: normal;
  font-size: 1rem;
  line-height: 1.42;
}

.container {
  max-width: 940px;
  margin: 0 auto;
  height: 100%;
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
}

.container-center {
  justify-content: center;
  text-align: center;
}

.section {
  height: 100vh;
  min-height: 650px;
  background: center center / cover no-repeat;
  position: relative;
}

.section-title {
  font-family: 'gagalin';
  font-size: 4rem;
  font-weight: normal;
  font-style: normal;
  text-transform: uppercase;
  color: #f9b43b;
  line-height: 1;
  margin-bottom: 1.38889rem;
  text-align: center;
}

.wrapper {
  position: relative;
  min-height: 100%;
  overflow: hidden;
  height: 100vh;
}

.maincontent {
  position: relative;
  top: 0;
  transition: top 0.5s;
  transition-timing-function: ease;
}

body {
  font-family: 'ubuntu', Helvetica, sans-serif;
  font-weight: 300;
  font-style: normal;
  font-size: 1rem;
  line-height: 1.42;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.block-scroll {
  overflow: hidden;
}

/* Blocks */

.points {
  position: fixed;
  top: 80px;
  right: 50px;
  z-index: 2;
}

.points__list {
  display: flex;
  flex-direction: column;
}

.points__item {
  margin-bottom: 5px;
}

.points__item:hover .points__link,
.points__item.points__item-active .points__link {
  border-color: #fff;
}

.points__link {
  display: block;
  height: 16px;
  width: 16px;
  border-radius: 50%;
  border: 1px solid transparent;
  position: relative;
}

.points__link:after {
  content: "";
  position: absolute;
  height: 6px;
  width: 6px;
  border-radius: 50%;
  background-color: #fff;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes lightSpeedIn {
  from {
    transform: translate3d(100%, 0, 0) skewX(-30deg);
    opacity: 0;
  }

  60% {
    transform: skewX(20deg);
    opacity: 1;
  }

  80% {
    transform: skewX(-5deg);
    opacity: 1;
  }

  to {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

.lightSpeedIn {
  animation-name: lightSpeedIn;
  animation-timing-function: ease-out;
  animation-duration: 1s;
}

@keyframes lightSpeedInLeft {
  from {
    transform: translate3d(-100%, 0, 0) skewX(30deg);
    opacity: 0;
  }

  60% {
    transform: skewX(-20deg);
    opacity: 1;
  }

  80% {
    transform: skewX(5deg);
    opacity: 1;
  }

  to {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

.lightSpeedInLeft {
  animation-name: lightSpeedInLeft;
  animation-timing-function: ease-out;
  animation-duration: 1s;
}

.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 30;
  top: 0;
  left: -9999px;
  height: 100vh;
  width: 100vw;
  background-color: rgba(47, 50, 52, 0.92);
}

.modal_active {
  left: 0;
}

.modal__inner {
  max-width: 460px;
  border-radius: 5px;
  background-color: #fff;
  padding: 20px 40px 20px 20px;
  position: relative;
}

.modalForm__inner {
  max-width: 460px;
  border-radius: 5px;
  background-color: #fff;
  padding: 20px;
  text-align: center;
}

.modal-black .reviews-team-title {
  color: #2f3234;
}

.modal-black .reviews__desc {
  margin-bottom: 0;
}

.modal-black .reviews__desc p {
  color: #2f3234;
}

.modal__close {
  position: absolute;
  top: 10px;
  right: 10px;
  height: 18px;
  width: 18px;
}

.modal__close:after {
  content: "";
  position: absolute;
  top: 7px;
  left: 0;
  height: 4px;
  width: 18px;
  background-color: rgba(227, 80, 40, 0.7);
  transform: rotate(45deg);
}

.modal__close:before {
  content: "";
  position: absolute;
  bottom: 7px;
  left: 0;
  height: 4px;
  width: 18px;
  background-color: rgba(227, 80, 40, 0.7);
  transform: rotate(-45deg);
}

.modalForm__close {
  display: inline-block;
  margin-top: 15px;
}

.modalForm__close:before {
  content: "Закрыть";
}

/* Sections */

.container-head {
  align-items: center;
}

.head {
  position: relative;
}

.head:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(20, 20, 20, 0.31);
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 0.72222rem;
  font-weight: 500;
  padding-top: 0.72222rem;
  width: 100%;
}

.logo {
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo-icon {
  width: 5.11111rem;
  height: 3.83333rem;
}

.head__buttons {
  display: flex;
  align-items: center;
}

.nav {
  margin-right: 17px;
}

.nav__list {
  display: flex;
  align-items: center;
}

.nav__item,
.hamburger-menu__item {
  display: flex;
  align-items: center;
}

.nav__item:after,
.hamburger-menu__item:after {
  content: "";
  display: block;
  height: 0.22222rem;
  width: 0.22222rem;
  border-radius: 50%;
  background-color: #fff;
  margin: 0 0.83333rem;
}

.nav__item:last-child:after,
.hamburger-menu__item:last-child:after {
  display: none;
}

.nav__link,
.hamburger-menu__link {
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
}

.hamburger-menu-link {
  width: 24px;
  height: 22px;
  margin-left: 30px;
  position: relative;
  z-index: 11;
  display: none;
  vertical-align: middle;
}

.hamburger-menu-link__item {
  background-color: #FFF;
  position: absolute;
  border-radius: 2px;
  transition: 0.3s cubic-bezier(0.8, 0.5, 0.2, 1.4);
  width: 100%;
  height: 4px;
  transition-duration: 500ms;
}

.hamburger-menu-link__item:nth-child(1) {
  top: 0px;
  left: 0px;
}

.hamburger-menu-link__item:nth-child(2) {
  top: 9px;
  left: 0px;
}

.hamburger-menu-link__item:nth-child(3) {
  bottom: 0px;
  left: 0px;
}

.hamburger-menu-link-active .hamburger-menu-link__item:nth-child(1) {
  transform: rotate(45deg);
  top: 9px;
}

.hamburger-menu-link-active .hamburger-menu-link__item:nth-child(2) {
  transform: scale(0.1);
}

.hamburger-menu-link-active .hamburger-menu-link__item:nth-child(3) {
  transform: rotate(-45deg);
  top: 9px;
}

.hamburger-menu {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 10;
  top: 0;
  left: -9999px;
  height: 100vh;
  width: 100vw;
  background-color: #2f3234;
}

.logo-overlay {
  position: absolute;
  top: 12px;
  left: 27px;
  z-index: 11;
}

.hamburger-menu-active {
  left: 0;
}

.hamburger-menu__list {
  flex-grow: 1;
}

.hamburger-menu__item {
  flex-direction: column;
  opacity: 0;
  animation-fill-mode: forwards;
  margin-bottom: 20px;
}

.hamburger-menu__item:after {
  height: 0.38889rem;
  width: 0.38889rem;
}

.hamburger-menu__item:last-child {
  margin-bottom: 0;
}

.hamburger-menu__item:last-child .hamburger-menu__link {
  margin-bottom: 0;
}

.hamburger-menu__link {
  margin-bottom: 1.11111rem;
  font-weight: normal;
  font-size: 1.33333rem;
  display: block;
  width: 100%;
  text-align: center;
}

.hamburger-menu__link:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.button,
.modalForm__close {
  border: none;
  cursor: pointer;
  text-transform: uppercase;
  text-decoration: none;
  display: inline-block;
  vertical-align: middle;
  padding: 0.55556rem 0.83333rem;
  background-color: #e45028;
  border-radius: 0.27778rem;
  transition: 0.2s;
  font-size: 0.72222rem;
  font-weight: 500;
  color: #fff;
}

.button:hover,
.modalForm__close:hover {
  background-color: #c1401e;
}

.head__tagline {
  margin: auto;
  display: flex;
  align-items: flex-end;
  padding-left: 0.88889rem;
  padding-bottom: 8%;
}

.head__img,
.head__title {
  width: 50%;
}

.head__img {
  margin-right: 0.33333rem;
}

.head__img-pic {
  display: block;
  max-width: 100%;
  max-height: 100%;
}

.head__title {
  padding-bottom: 3.55556rem;
  padding-left: 0.38889rem;
}

.section-title_head {
  text-align: initial;
}

.head__title-desc {
  font-family: 'lifehack';
  font-weight: normal;
  font-style: normal;
  font-size: 3.61111rem;
  text-transform: lowercase;
  color: #fff;
  letter-spacing: -0.11111rem;
  line-height: 0.92;
}

.head__down {
  width: 2.22222rem;
  height: 1.94444rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.66667rem;
}

.arrow-scroll-icon {
  width: 1.61111rem;
  height: 0.83333rem;
}

.section-title_black {
  margin-bottom: 2.33333rem;
  color: #2f3234;
}

.best {
  color: #2f3234;
}

.best__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.best__item {
  width: calc(100% / 3 - 1.11rem);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.best__icon {
  width: 5.38889rem;
  height: 4.94444rem;
  margin-bottom: 1.83333rem;
}

.meat-icon {
  width: 5.38889rem;
  height: 4.94444rem;
}

.hand-icon {
  width: 4.05556rem;
  height: 4.77778rem;
}

.natural-icon {
  width: 4.88889rem;
  height: 4.22222rem;
}

.best__title {
  font-family: 'lifehack';
  font-weight: normal;
  font-style: normal;
  font-size: 1.33333rem;
  text-align: center;
  letter-spacing: -0.05556rem;
  margin-bottom: 1.11111rem;
}

.best__desc p {
  text-align: justify;
  font-family: 'ubuntu';
  font-weight: 300;
  font-style: normal;
  font-size: 0.77778rem;
  opacity: 0.8;
  line-height: 1.71;
}

.reviews__list {
  display: flex;
  flex-wrap: wrap;
  height: 100%;
}

.reviews__item {
  flex: 1 1 25%;
  height: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  filter: grayscale(100%);
}

.reviews__item:hover .reviews__wrap {
  opacity: 1;
}

.reviews__wrap {
  padding: 2.77778rem 1.05556rem;
  max-height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  opacity: 0;
  transition: .2s;
  width: 100%;
  height: 100%;
  background-color: rgba(20, 20, 20, 0.7);
}

.reviews-team-title {
  font-family: 'gagalin';
  font-weight: normal;
  font-style: normal;
  font-size: 0.77778rem;
  text-transform: uppercase;
  color: #fff;
  margin-bottom: 1.66667rem;
}

.reviews__desc {
  overflow: hidden;
  margin-bottom: 16px;
}

.reviews__desc p {
  font-family: 'ubuntu';
  font-weight: 300;
  font-style: normal;
  font-size: 0.77778rem;
  color: #fff;
}

.button_color-black {
  background-color: #2f3234;
}

.button_color-black:hover {
  background-color: #1f2122;
}

.player {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.player__wrap {
  width: 660px;
  height: 405px;
  margin: 0 auto;
}

.player__video {
  max-width: 100%;
  max-height: 100%;
}

.container-order {
  justify-content: center;
}

.section-title_order {
  margin-bottom: 2.33333rem;
}

.form {
  display: flex;
}

.form__column {
  flex: 1;
  margin-right: 1.11111rem;
}

.form__column:last-child {
  margin-right: 0;
}

.form__row {
  display: flex;
  margin-bottom: 1rem;
}

.form__row:last-child {
  margin-bottom: 0;
}

.form__label {
  flex: 1;
  margin-right: 1.11111rem;
}

.form__label:last-child {
  margin-right: 0;
}

.form__input,
.form__textarea {
  width: 100%;
  outline: none;
  padding: 0.38889rem;
  border-radius: 0.27778rem;
  border: none;
}

.form__textarea {
  resize: none;
  height: 5.83333rem;
}

.form__title-elem {
  color: #fff;
  font-size: 0.77778rem;
  margin-bottom: 0.27778rem;
}

.form__label_row {
  display: flex;
  align-items: center;
}

.form__label_row {
  flex: 0;
  cursor: pointer;
}

.form__label_row .form__title-elem {
  margin-bottom: 0;
  white-space: nowrap;
